@value homeBlack from "../../common/colors.css";

.lunch {
  width: 200px;
  background-color: antiquewhite;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 15px;
  box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.75);
  padding: 10px 0;
  position: absolute;
  color: homeBlack;
  right: 20px;
  top: 220px;
}

.name {
  font-weight: bold;
  margin-top: 5px;
}

.menuList {
  list-style: none;
  padding-left: 0;
}

@media screen and (min-width: 1500px) and (max-width: 1800px) {
  .lunch {
    right: 400px;
    top: 10px;
  }
}

@media screen and (max-height: 800px) {
  .lunch {
    right: 20px;
    top: 300px;
  }
}
